import React,{Component} from 'react'
import {Link} from "react-router-dom";
import '../../css/bookkeeping.css'
class notlog extends Component{
    bottom = [
        {
            path:`/body/detail`,
            background:{ background:`url(${require('../../img/6.png')}) no-repeat center 60%/80%`},
            name:'明细',key:1},
        {
            path:`/body/table`,
            background:{ background:`url(${require('../../img/2.png')}) no-repeat center 90%/80%`},
            name:'超表',key:2},
        {
            path:`/body/bookkeeping`,
            background:{ background:`url(${require('../../img/5.png')}) no-repeat center 10%/80%`},
            name:'记账',key:3},
        {
            path:`/body/ranking`,
            background:{ background:`url(${require('../../img/3.png')}) no-repeat center 10%/80%`},
            name:'榜单',key:4},
        {
            path:`/me`,
            background:{ background:`url(${require('../../img/4.png')}) no-repeat center 40%/80%`},
            name:'我的',key:5}
    ]
    render() {
        return(
            <div className='bottom-body'>
                {this.bottom.map((data,key)=>{
                    return(
                        <label>
                            <Link className='bottom-list' key={data.key} style={data.background}  to={data.path}>
                                <div><span>{data.name}</span></div>
                            </Link>
                        </label>
                    )
                })}
            </div>
        )
    }
}
export default notlog