import React,{Component} from 'react'
import { Select } from 'antd';
import {Link} from "react-router-dom";
import { createHashHistory } from 'history'
import ajax from "../../../../ajax";
const { Option } = Select;
class editAccCount extends Component{
    constructor(props) {
        super(props);
        this.state={
            id:'',
            name:'',
            type:'',
            initial_balance:0,
            remark:'',
            sort:10,
            account:'',
            typeName:''
        }
        let newId = this.props.match.params.id;
        ajax('get',`api/account/detail?id=${newId}&`,(data)=>{
            if (data.status){
                this.setState({id:data.data.id,
                    name:data.data.name,
                    initial_balance:data.data.initial_balance,
                    type:data.data.type})
                let type = data.data.type
                if(type==1){this.setState({typeName:'现金'})}
                else if(type==2){this.setState({typeName:'银行'})}
                else if(type==3){this.setState({typeName:'支付平台'})}
                else if(type==4){this.setState({typeName:'其他'})}
            }
        })
    }
    onChange=(value)=>{
        this.setState({type:value})
    }
    submit=()=>{
        ajax('post',`api/account/update?id=${this.state.id}&`,(data)=>{
            console.log(data)
            if (data.status){
                alert('修改成功')
                createHashHistory().push('/me/myvip')
            }else{
                alert(data.data)
            }
        },this.state,(err)=>{
            console.log(err)
        })
    }
    render() {
        return (
            <div className={'account-add-all'}>
                <div className={'top'}>
                    <Link className={'fl'} to={'/me/myvip'}>
                        <span className={'iconfont'}>&#xe609;</span>
                        <span>返回</span>
                    </Link>
                    <span className={'top-name'}>修改头像</span>
                    <span className={'add-btn'} onClick={this.submit}>
                          修改
                    </span>
                </div>
                <ul className={'add-account'}>
                    <li className={'account-name'}>
                        <span>账户名 :</span>
                        <input  type="text"
                                onChange={(r) => {
                                    this.setState({name: r.target.value})
                                }}
                                placeholder={this.state.name}/>
                    </li>
                    <li className={'add-type'}>
                        <span>类型名 :</span>
                        <Select
                            showSearch
                            defaultValue={'可选'}
                            optionFilterProp="children"
                            onChange={this.onChange}
                            filterOption={(input, option) =>
                                option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                            }
                        >
                            <Option value="1">现金</Option>
                            <Option value="2">银行</Option>
                            <Option value="3">支付平台</Option>
                            <Option value="4">其他</Option>
                        </Select>
                        <span className={'old-type'}>原类型:<span>{this.state.typeName}</span></span>
                    </li>

                    <li className={'account-name'}>
                        <span>初余额 :</span>
                        <input  type="text"
                                onChange={(r) => {
                                    this.setState({initial_balance: r.target.value})
                                }}
                                placeholder={'选填'}/>
                    </li>
                    <li className={'account-name'}>
                        <span>写备注 :</span>
                        <input  type="text"
                                onChange={(r) => {
                                    this.setState({remark: r.target.value})
                                }}
                                placeholder={'选填'}/>
                    </li>
                </ul>
            </div>
        )
    }
}
export default editAccCount