import React, {Component} from 'react';
import ajax from '../../../ajax'
import {message} from "antd";
import {Link} from "react-router-dom";
import axios from "axios";
import {createHashHistory} from "history";
//修改个人信息,并上传头像
class Upimg extends Component {
    constructor(props) {
        super(props);
        this.fileInput = React.createRef();
        this.state = {
            username:'',
            avatar:'',
            imgurl:''
        }
    }
    submit = () => {
        //获取对象\
        let formData = new FormData();
        //获得文件信息
        let fileObj = this.fileInput.current.files[0]
        formData.append('file',fileObj)
        console.log(formData)
        let theUrl=''
        if (localStorage.getItem('token')!=null){
            theUrl='http://jizhang-api-dev.it266.com/api/upload/image?token='+localStorage.getItem('token')
        }
        axios({
            method:'post',
            url:theUrl,
            headers:{
                "Content-Type": "multipart/form-data"
            },
            data:formData
        }).then((res)=>{
            console.log(res)
            if (res.status==200){
                this.setState({avatar:res.data.data.file.fileKey,imgurl:res.data.data.file.thumbnailUrl._temp})
            }
        })
    };
    //保存提交
    up = ()=>{
        ajax('post','api/user/profile/update?',(res)=>{
            if(res.status){
                message.success('修改成功')
                createHashHistory().push('/me')
            }else{
                alert(res.data)
            }
        },this.state)
    }
    render() {
        return(
            <div className={'upimage'}>
                <div className={'top'}>
                    <Link className={'fl'} to={'/me/myvip'}>
                        <span className={'iconfont'}>&#xe609;</span>
                        <span>返回</span>
                    </Link>
                    <span className={'top-name'}>修改账户</span>
                    <span className={'add-btn fr'} onClick={this.up}>
                          修改
                    </span>
                </div>
                <div className={'body'} >
                    <div className={'txt'}>
                        <div className={'input'}>
                            <span className={'contents c1'}>头像:</span>
                            <span className={'upfile'}>
                                {
                                    this.state.imgurl==''?<div><span>+</span><input type="file" name={'file'}  ref={this.fileInput} onChange={this.submit}/></div>:<img src={this.state.imgurl} width={90} height={90} className={'img'}/>
                                }
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Upimg;