import React,{Component} from 'react'
import {BrowserRouter as Router,Switch,Route} from "react-router-dom";
import Bottom from '../bottom'
import Me from './body'
import Top from './top'
import Myvip from './myvip'
import AddAcount from './myvip/add'
import EditAcount from './myvip/edit'
import LogOut from './logout'
import FeedBack from './feedback'
import UpImg from './upimg'
class notlog extends Component{
        bgname= {zIndex:'-1',position:'fixed',top:'0',left:'0',width:'100%',height:'1000px',background:`url(${require('../../img/bg.jpg')}) no-repeat center -4rem/150%` }
    render() {
        return(
            <Switch>
                <Route exact path={'/me'}>
                    <div>
                        <Top></Top>
                        <Me></Me>
                        <Bottom></Bottom>
                        <div style={this.bgname}></div>
                    </div>
                </Route>
                <Route exact path={'/me/myvip'}><Myvip></Myvip></Route>
                <Route exact path={'/me/upimg'}><UpImg></UpImg></Route>
                <Route path={'/me/myvip/add'}><AddAcount></AddAcount></Route>
                <Route path={'/me/myvip/edit/:id'} component={EditAcount}/>
                <Route path={'/me/logout'}><LogOut></LogOut></Route>
                <Route path={'/me/feedback'}><FeedBack></FeedBack></Route>
            </Switch>
        )
    }
}
export default notlog