import axios from "axios";
import qs from 'qs';
var ajax = function (method,url,callback,data=null,err) {
    let theUrl=''
    if (localStorage.getItem('token')!=null){
        theUrl='http://jizhang-api-dev.it266.com/'+url+'token='+localStorage.getItem('token')
    }else{
        theUrl='http://jizhang-api-dev.it266.com/'+url
    }
    axios({
        method:method,
        url:theUrl,
        headers:{
            "Content-Type": "application/x-www-form-urlencoded"
        },
        data:qs.stringify(data)
    }).then((response)=>{
        callback(response.data)
    })
}
export  default ajax