import React,{Component} from 'react'
import {Link} from "react-router-dom";
import '../../../css/bookkeeping.css'
import '../../../css/icon/iconfont.css'
import Abook from './accountBook'
import ajax from "../../../ajax";
class metop extends Component{
    constructor(props) {
        super(props)
        this.state = {
            isToggleOn: true,
            dispaly: 'none',
            year: '',
            month: '',
            ina:'',
            inb:'',
            outa:'',
            outb:''
        }
        this.handleClick = this.handleClick.bind(this)
    }
    handleClick(){
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn,
            display: prevState.isToggleOn ? 'block': 'none'
        }));
    }
    componentDidMount() {
        if (localStorage.getItem('token')!=null){
            ajax('get',
                'api/view/home?',
                (data)=>{
                    // console.log(data)
                    let rows = data.data.account
                    let arr = rows.month.split('-')
                    let ins = rows.in.split('.')
                    let outs = rows.out.split('.')
                    // console.log(ins,outs)
                    this.setState({year:arr[0]})
                    this.setState({month:arr[1]})
                    this.setState({ina:ins[0]})
                    this.setState({inb:ins[1]})
                    this.setState({outa:outs[0]})
                    this.setState({outb:outs[1]})
                })
        }
    }
    render() {
        return(
            <div className='detail-top'>
                <div style={{display: this.state.display}} className='abook-all'>
                    <Abook></Abook>
                    <div className='abook-right' onClick={this.handleClick}></div>
                </div>
                <div className='brand-nav'>
                    <span className='iconfont' onClick={this.handleClick}>&#xe607;</span>
                    <span className='brand' >cokia·记账</span>
                </div>
                <ul className='detail-over-view cf'>
                    <li><p>{this.state.year}年</p><span className='big-money'>{this.state.month}</span><span>月</span></li>
                    <li><p>收入</p><span className='big-money'>{this.state.ina}</span><span>.{this.state.inb}</span></li>
                    <li><p>支出</p><span className='big-money'>{this.state.outa}</span><span>.{this.state.outb}</span></li>
                </ul>
                <div className='detail-chart'>
                    <Link to='/body/detail/category' className='iconfont'>&#xe66e;</Link>
                    <span className='iconfont'>&#xe60a;</span>
                    <span className='iconfont'>&#xe60c;</span>
                    <span className='iconfont'>&#xe629;</span>
                    <span className='iconfont'>&#xe614;</span>
                </div>
            </div>
        )
    }
}
export default metop