import React,{Component} from 'react'
import {Link} from "react-router-dom";
import creatHistory from 'history/createHashHistory'
import '../../css/bookkeeping.css'
import '../../css/icon/iconfont.css'
import Cate from './category'
const history = creatHistory()
class book extends Component{
    render() {
        if (localStorage.getItem('token')!=null){
            return (<Cate></Cate>)
        }else {
            return (
                <div className='bookkeeping'>
                    <div className='book-nav'>
                        <Link className='iconfont' onClick={history.goBack}>&#xe609;</Link>
                        <span className='book-login'>登录</span>
                    </div>
                    <div className='book-body'>
                        <img src={require('../../img/book_log.gif')} alt=""/>
                        <p>嗨!你的记账</p>
                        <p className='book-login-txt'>不,是你的记账</p>
                        <Link to='/log' className='notlog-book-button'>微信登录</Link>
                    </div>
                </div>
            )
        }
    }
}
export default book