import React,{Component} from 'react'
import {Link,Switch,Route} from "react-router-dom";
import 'antd/dist/antd.css';
import axios from 'axios'
import ajax from "../../../ajax";
import qs from "qs";
import Out from './out'
import In from './in'
class category extends Component{
    constructor(props){
        super(props)
        this.state = {
            name:'',
            radio:'2',
            osty:{backgroundColor:'red'},
            isty:{backgroundColor:'rgba(0,0,0,0)'},
            display:'none',
            in:''
        }
    }
    submit=()=>{
        ajax('post','api/category/create',(data)=>{
         console.log(data)
            if (data.status){
                alert('添加成功')
            }else{
                alert(data.data)
            }
        },{parent_id:0,type:this.state.radio,name:this.state.name,sort:1},(err)=>{
            alert(err)
        })
    }
    out=()=>{
        return('aaa')
    }
    render() {
        return(
            <div className={'category-all'}>
                <div className='category-top'>
                    <p className='top-nav'>
                            <Link className={'fl'}  to={'/body/detail'}>
                                <span className='iconfont'>&#xe609;</span>
                                返回
                            </Link>
                        <span className={'nav-name'}>个人账本-消费</span>
                    </p>
                    <div className={'top-select'}>
                        <span className={'tar'}>
                            <Link to={'/body/bookkeeping'}>支出</Link>
                        </span>
                        <span>
                            <Link to={'/body/bookkeeping/income'}>收入</Link>
                        </span>
                        <Switch>
                            <Route exact path={'/body/bookkeeping'}>
                                <div className={'category-select-bottom'}><span style={{float:'left'}}></span></div>
                            </Route>
                            <Route exact path={'/body/bookkeeping/income'}>
                                <div className={'category-select-bottom'}><span style={{float:'right'}}></span></div>
                            </Route>
                        </Switch>
                    </div>
                </div>
                <div className={'category-body'}>
                    <Switch>
                        <Route exact path={'/body/bookkeeping'}>
                            <Out></Out>
                        </Route>
                        <Route exact path={'/body/bookkeeping/income'}>
                            <In></In>
                        </Route>
                    </Switch>
                </div>
            </div>
        )
    }
}
export default category