import React,{Component} from 'react'
import {Link} from "react-router-dom";
import '../../../../css/bookkeeping.css'
import '../../../../css/icon/iconfont.css'
import ajax from "../../../../ajax";
import { Popconfirm, message } from 'antd';
class abook extends Component{
    constructor(props) {
        super(props)
        this.state = {
            isToggleOn: true,
            dispaly: 'block',
            bookname:'',
            default:'',
            set:{display:'none'},
            setToggle:true,
            changeToggle:true,
            change:{display:'none'},
            book_id:'',
            book_name:'',
            }
        this.handleClick = this.handleClick.bind(this)
    }
    handleClick() {
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn,
            display: prevState.isToggleOn ? 'none': 'block'
        }));
    }
    confirm=(e)=>{
        ajax('POST','api/book/delete?',(data)=>{
            if(data.status){
                message.success('删除成功');
                setTimeout(
                    ()=>{
                        window.location.reload()
                    },100)
            }else{
                alert(data.data)
            }
        },{book_id:this.state.book_id},(err)=>{
            alert(err)
        })
    }
    componentDidMount() {
        ajax('GET','api/book?',(data)=>{
            this.setState({bookname:data.data})
        })
        ajax('get','api/book/get-default?',(data)=>{
            this.setState({default:data.data})
        },'',(err)=>{
            alert(err)
        })
    }
    update=()=>{
        ajax('POST','api/book/update?',(data)=>{
            console.log(data)
            message.success('修改成功');
            setTimeout(
                ()=>{
                    window.location.reload()
                },100)
        },this.state)
    }
    render() {
        if (localStorage.getItem('token')!=null){
            return(
            <div className='abook-body'>
                <div className='abook-left'>
                    <h2>账本列表</h2>
                    <ul className={'abook-list'}>
                        {
                            this.state.bookname && this.state.bookname.map((data) => {
                                if(data.id==this.state.default.id) {
                                    return(
                                        <li>
                                            <span>{data.name}</span>
                                            <span className='default'>当前</span>
                                        </li>
                                    )
                                }else{
                                   return(
                                       <li>
                                            <span
                                                className={'other'}
                                                onClick={()=>{
                                                    if (this.state.setToggle){
                                                        (
                                                            this.setState({
                                                            set:{display:'block'}
                                                            ,book_id:data.id,setToggle:false}))
                                                    }else{
                                                        (
                                                            this.setState({
                                                            set:{display:'none'}
                                                            ,book_id:data.id,setToggle:true,
                                                            changeToggle:true,
                                                            change:{display:'none'},})
                                                        )
                                                    }
                                                }}>
                                                {data.name}
                                            </span>
                                            <span className='to-default'onClick={()=>{
                                                ajax('POST',
                                                    'api/book/set-default?',
                                                    (data)=>{
                                                        if(data.status){
                                                            window.location.reload()
                                                        }else{
                                                            alert(data.data)
                                                        }
                                                    },{book_id:data.id}
                                                )
                                            }}>设为当前</span>
                                           <div className={'book-set'}  style={this.state.set}>
                                               <Popconfirm
                                                   className={'book-delete'}
                                                   title="Are you sure delete this task?"
                                                   onConfirm={this.confirm}
                                                   okText="Yes"
                                                   cancelText="No"
                                               ><a href="#">
                                                       删除
                                                   </a>
                                               </Popconfirm>
                                               <span className={'set-name'} onClick={
                                                   ()=>{
                                                       if(this.state.changeToggle){
                                                           this.setState({change:{display: 'block'},
                                                               changeToggle:false})
                                                       }else{
                                                           this.setState({change:{display: 'none'},
                                                               changeToggle:true})
                                                       }

                                                   }
                                               }>
                                                   改名
                                               </span>
                                               <div className={'change-name'} style={this.state.change}>
                                                   <input onChange={(e)=>{
                                                       this.setState({book_name:e.target.value})}
                                                   } type="text" />
                                                   <span className={'change'} onClick={this.update}>
                                                       更改
                                                   </span>
                                                   <span className={'exit'}
                                                         onClick={
                                                             ()=>{
                                                                 this.setState({change:{display: 'none'}})
                                                             }
                                                         }>
                                                       取消
                                                   </span>
                                               </div>

                                           </div>
                                       </li>
                                   )
                                }
                            })
                        }
                    </ul>
                    <div className='abook-bottom'>
                        <Link to={'/body/detail/editBook'} className='iconfont abook-left-set'>
                            &#xe617;管理
                        </Link>
                        <Link to='/body/detail/addBook'><
                            span className='abook-left-plus'>＋</span>添加
                        </Link>
                    </div>
                </div>
            </div>
        )}else{
            return (<div className='abook-body'><div className='abook-left'><h2>登录后再来查看吧</h2></div></div>)
        }
    }
}
export default abook