import React,{Component} from 'react'
import {HashRouter,Switch,Route} from "react-router-dom";
import '../../css/bookkeeping.css'
import '../../css/icon/iconfont.css'
import Bottom from '../bottom'
import Top from './top'
import Body from './body'
import Category from './category'
import AddBook from './top/addbook'
import Edit from './body/edit'
class detail extends Component{
    height = window.screen.availHeight;
    section = {
        width: "100%",
        height: ((this.height)*1)+'px'
    }
    render() {
        return(
          <Switch>
              <Route exact path={'/'}>
                  <div className='detail-all' style={this.section}>
                      <Bottom></Bottom>
                      <Top></Top>
                      <Body/>
                  </div>
              </Route>
              <route exact path={'/body/detail'}>
                  <div className='detail-all'>
                    <Bottom></Bottom>
                    <Top></Top>
                    <Body/>
                  </div>
              </route>
              <route path={'/body/detail/category'}>
                  <Category></Category>
              </route>
              <route exact path={'/body/detail/addBook'}>
                  <AddBook></AddBook>
              </route>
              <Route path='/body/detail/edit/:id' component={Edit}/>
          </Switch>
        )
    }
}
export default detail