import React,{Component} from 'react'
import {Link} from "react-router-dom";
import '../../../css/bookkeeping.css'
import '../../../css/icon/iconfont.css'
class notme extends Component{
    list =[
        {demo:'space',class:``,icon:``,content:'',to:'/myvip',bottom:'bottom-none'},
        {demo:'pace',class:`iconfont`,icon:`\ue6c4`,content:'我的账户',to:'/me/myvip',bottom:'bottom-border',right:'\ue609'},
        {demo:'pace',class:`iconfont`,icon:`\ue64d`,content:'邀请好友',to:'/myvip',bottom:'bottom-border',right:'\ue609'},
        {demo:'pace',class:`iconfont`,icon:`\ue603`,content:'皮肤设置',to:'/myvip',bottom:'bottom-none',right:'\ue609'},
        {demo:'space',class:``,icon:``,content:'',to:'/myvip',bottom:'bottom-none'},
        {demo:'pace',class:`iconfont`,icon:`\ue61e`,content:'公众号',to:'/myvip',bottom:'bottom-border',right:'\ue609'},
        {demo:'pace',class:`iconfont`,icon:`\ue612`,content:'联系客服',to:'/myvip',bottom:'bottom-none',right:'\ue609'},
        {demo:'space',class:``,icon:``,content:'',to:'/myvip',bottom:'bottom-none'},
        {demo:'pace',class:`iconfont`,icon:`\ue683`,content:'意见反馈',to:'/me/feedback',bottom:'bottom-none',right:'\ue609'},
        {demo:'space',class:``,icon:``,content:'',to:'/myvip',bottom:'bottom-none'},
        {demo:'pace',class:`iconfont`,icon:`\ue674`,content:'退出登录',to:'/log',bottom:'bottom-none',right:'\ue609'},
        {demo:'space',class:``,icon:``,content:'',to:'/myvip',bottom:'bottom-none'}
    ]
    render() {
        if (localStorage.getItem('token')!=null){
            return (
                <div>
                    <ul className='me-count cf'>
                        <li><span className='count-red'>0</span><span>已邀请</span></li>
                        <li><span className='count-pink'>0</span><span>总账本</span></li>
                        <li><span className='count-pur'>0</span><span>共记账</span></li>
                    </ul>
                    <ul className='notlog-me'>
                        {
                            this.list.map(function(data,index){
                                return(
                                    <li key={index} className={data.demo}>
                                        <Link to={data.to} key={index} className={data.bottom}>
                                            <span className={data.class}>{data.icon}</span>
                                            <span className='me-content'>{data.content}</span>
                                            <span className={data.class}>{data.right}</span>
                                        </Link>
                                    </li>
                                )
                            })}
                    </ul>
                </div>
            )
        }else{
            return(
                <div>
                    <ul className='me-count cf'>
                        <li><span className='count-red'>0</span><span>已邀请</span></li>
                        <li><span className='count-pink'>0</span><span>总账本</span></li>
                        <li><span className='count-pur'>0</span><span>共记账</span></li>
                    </ul>
                    <ul className='notlog-me'>
                        {
                            this.list.map(function(data,index){
                                return(
                                    <li key={index} className={data.demo}>
                                        <Link to='/log' key={index} className={data.bottom}>
                                            <span className={data.class}>{data.icon}</span>
                                            <span className='me-content'>{data.content}</span>
                                            <span className={data.class}>{data.right}</span>
                                        </Link>
                                    </li>
                                )
                            })}
                    </ul>
                </div>
        )}
    }
}
export default notme