import { DatePicker } from 'antd';
import React,{Component} from 'react'
import axios from "axios";
import qs from "qs";
import ajax from "../../../../ajax";
import { Select,message  } from 'antd';
const { RangePicker } = DatePicker;
const { Option } = Select;
class out extends Component{
    constructor(props) {
        super(props);
        this.state={
            status:true,
            out:'',
            display:'none',
            transfrom:{transform: 'translateY(150%)'},
            name:'',
            date:0,
            total_money:0,
            money:0,
            category_id:0,
            bookName:'',
            account_id:0
        }
        let theUrl=''
        if (localStorage.getItem('token')!=null){
            theUrl='http://jizhang-api-dev.it266.com/api/category?token='
                +localStorage.getItem('token')+'&type='
        }else{

        }
        axios({
            method:'get',
            url:theUrl+'2',
            headers:{
                "Content-Type": "application/x-www-form-urlencoded"
            },
            data:qs.stringify({type: 1})}).then((response)=>{
            this.setState({out:response.data})
        })

    }
    Change=(value)=>{
        this.setState({account_id:value})
    }
    onChange=(value, dateString)=>{
        let date = dateString.split(' ')
        this.setState({date:date[0]})
    }
    submit=()=>{
        let nowDate = ''
        if(this.state.date===0){
            let date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();
            if (month < 10) {
                month = "0" + month;
            }
            if (day < 10) {
                day = "0" + day;
            }
             nowDate = year + "-" + month + "-" + day;
        }
        if(this.state.category_id===0||this.state.account_id===0||this.state.total_money===0){
            alert('请补全信息')
            return false
        }
        this.setState({date:nowDate}
        ,this.add)
    }
    add=()=>{
        setTimeout(
                ajax('POST','api/record/create?',(data)=>{
                    if(data.status){
                        message.success('添加成功');
                        window.location.href='/body/detail'
                    }else{
                        console.log(this.state)
                        alert(data.data)
                    }
                },this.state)
            , 10)
    }
    componentDidMount() {
        ajax('get','api/account?',(data)=>{
            this.setState({bookName:data.data})
        })
    }
    render() {
        return(
            <ul className='category-body-ul'>
                {
                    this.state.out.data&&this.state.out.data.map((data)=>{
                        return(<li onClick={()=>{
                            if(this.state.status){
                                this.setState({category_id:data.id,transfrom:{transform: 'translateY(0%)'},status:false,name:data.name})
                            }else{
                                this.setState({category_id:data.id,transfrom:{transform: 'translateY(150%)'},status:true})
                            }
                        }}>
                            <span className='iconfont iconfonts'>&#xe605;</span>
                            <span>{data.name}</span>
                            <span className='iconfont'>＋</span>
                        </li>)
                    })
                }
                <div className={'date-select'} style={this.state.transfrom}>
                    <input type="text" className={'money-accout'} placeholder={'总价多少钱呢'}
                    onChange={(v)=>{
                        this.setState({total_money:v.target.value})}}/>
                    <input type="text" className={'money-accout'} placeholder={'花了多少钱呢'}
                           onChange={(v)=>{
                               this.setState({money:v.target.value})}}/>
                        <DatePicker showTime placeholder="选择日期" onChange={this.onChange} onOk={this.onOk} />

                        <div className={'date-select-submit'}
                            onClick={this.submit}>
                            添加
                        </div>

                    <Select
                        showSearch
                        style={{ width: 200 }}
                        placeholder="账户"
                        optionFilterProp="children"
                        onChange={this.Change}
                        filterOption={(input, option) =>
                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                        }>
                        {this.state.bookName&&this.state.bookName.map((data)=>{
                            return(
                                <Option key={data.id} value={data.id}>{data.name}</Option>
                            )
                        })}
                    </Select>
                    <div>
                        <div className={'tips'}><span className={'red'}>* </span>若不选择日期,则默认为当前日</div>
                        <div className={'fl'}>当前选择 : {this.state.name}</div>
                    </div>
                </div>
            </ul>
        )
    }
}
export default out