import React,{Component} from 'react'
import {HashRouter,Switch,Route} from "react-router-dom";
import '../css/bookkeeping.css'
import Me from './me'
import Detail from './detail'
import Book from './bookkeeping'
class body extends Component{
    render() {
        return(
                <Switch>
                    <Route exact path={'/'}><Detail></Detail></Route>
                    <Route  path={'/me'}><Me></Me></Route>
                    <Route  path={'/body/detail'}><Detail></Detail></Route>
                    <Route  path={'/body/complete'}><div></div></Route>
                    <Route  path={'/body/bookkeeping'}><Book/></Route>
                </Switch>
        )
    }
}
export default body