import React,{Component} from 'react'
import {Link} from "react-router-dom";
import { createHashHistory } from 'history'
import {Popconfirm, message, DatePicker} from 'antd';
import ajax from "../../../../ajax";
class body extends Component{
    constructor(props) {
        super(props);
        this.state={
            list:'',
            id:'',
            total_money:'',
            company_name:'',
            remark:''
        }
    }
    confirm=()=>{
        ajax('post',`api/record/item/delete?itemId=${this.state.id}&`,(data)=>{
            if (data.status){
                message.success('删除成功')
                setTimeout(()=>{
                    window.location.reload()
                },500)
            }else{
                message.error('删除失败');
            }
        })
    }
    cancel=()=>{
        message.error('Click on No');
    }
    componentDidMount() {
        ajax('get','api/record/account?',(data)=>{
            if (data.status){
                this.setState({list:data.data.list,id:this.props.match.params.id})
            }
        })
    }

    render() {
        return (
            <ul>
                {
                    this.state.list.map&&this.state.list.map((data)=>{
                        if(parseInt(this.state.id)==parseInt(data.id)){
                            return(
                                <div  className={'edit-list-detail'}>
                                    <div className={'top'}>
                                        <Link className={'fl'} to={'/body/detail'}>
                                            <span className={'iconfont'}>&#xe609;</span>
                                            <span>返回</span>
                                        </Link>
                                        <span className={'top-name'}>编辑账单</span>
                                    </div>
                                    <ul>
                                        <li>
                                            <span className={'total_money'}>总价</span><span>:</span>
                                            <input type="text" placeholder={data.total_money}
                                            onChange={(r)=>{this.setState({total_money:r.target.value})}}/>
                                        </li>
                                        <li>
                                            <span className={'paid_money'}>已支付</span>
                                            <i className={'red'}>*</i>
                                            <span>:</span>
                                            <span>{data.paid_money}</span>
                                        </li>
                                        <li>
                                            <span className={'remark'}>日期</span>
                                            <i className={'red'}>*</i>
                                            <span>:</span>
                                            <span>{data.date}</span>
                                        </li>
                                        <li>
                                            <span className={'company_name'}>交易对象</span><span>:</span>
                                            <input type="text" placeholder={data.company_name}
                                                   onChange={(r)=>{this.setState({company_name:r.target.value})}}/>
                                        </li>
                                        <li>
                                            <span className={'remark'}>备注</span><span>:</span>
                                            <input type="text" placeholder={data.remark}
                                                   onChange={(r)=>{this.setState({remark:r.target.value})}}/>
                                        </li>
                                    </ul>
                                    <div className={'tip'}><span>带 * 为不可更改</span></div>
                                    <p>
                                        <span className={'delete'}>
                                        <Popconfirm
                                            title="确定要删除此帐单?"
                                            onConfirm={this.confirm}
                                            onCancel={this.cancel}
                                            okText="是"
                                            cancelText="否"
                                            className={''}
                                        >
                                            <a className={'delete'} href="#">删除</a>
                                        </Popconfirm>
                                        </span>
                                        <span className={'save'} onClick={()=>{
                                            if(this.state.total_money==''&&
                                            this.state.company_name==''&&
                                            this.state.remark==''){
                                                alert('至少更改一项哦')
                                            }else{
                                                ajax('post',`api/record/update?id=${data.id}&`,(data)=>{
                                                    if (data.status){
                                                        message.success('修改成功')
                                                        createHashHistory().push('/body/detail/')
                                                    }
                                                },this.state)
                                            }
                                        }}
                                        >保存
                                        </span>
                                    </p>
                                </div>
                            )
                        }
                    })
                }
            </ul>
        )
    }
}
export default body