import React,{Component} from 'react'
import {Link} from "react-router-dom";
import { createHashHistory } from 'history'
import ajax from "../../../ajax";
class feedback extends Component{
    constructor(props) {
        super(props);
        this.state={
            contact:'',
            content:'',
        }
    }
    submit=()=>{
        ajax('post','api/feedback/add?',(data)=>{
            if (data.status){
                alert('发送成功')
                createHashHistory().push('/me')
            }else{
                alert(data.data)
            }
        },this.state)
    }
    render() {
        return (
            <div className={'account-add-all'}>
                <div className={'top'}>
                    <Link className={'fl'} to={'/me'}>
                        <span className={'iconfont'}>&#xe609;</span>
                        <span>返回</span>
                    </Link>
                    <span className={'top-name'}>意见反馈</span>
                    <span className={'add-btn'} onClick={this.submit}>
                          提交
                    </span>
                </div>
                <ul className={'add-account'}>
                    <li className={'account-name'}>
                        <span>联系方式 :</span>
                        <input  type="text"
                                onChange={(r) => {
                                    this.setState({contact: r.target.value})
                                }}
                                placeholder={'( 可 选 )'}/>
                    </li>
                    <li className={'feedback'}>
                        <span>反馈:</span>
                        <textarea cols="40" rows="3"
                                  onChange={(r) => {
                                      this.setState({content: r.target.value})
                                  }}
                        >  </textarea>
                    </li>
                </ul>

            </div>
        )
    }
}
export default feedback