import React,{Component} from 'react'
import {BrowserRouter as Router, Route,Link} from "react-router-dom";
import ajax from "../../../ajax";
import {Popconfirm, message, DatePicker} from 'antd';
import { Select } from 'antd';
const { Option } = Select;
class myvip extends Component{
    constructor(props) {
        super(props);
        this.state={
            list:'',
            tableStyle:{borderSpacing:'0 .3rem',borderCollapse:'separate'},
            id:'',
            name:'',
            transFrom:{transform: 'translateY(0%)'},
            type:1,
            initial_balance:0,
            remark:'',
            sort:10
        }
    }
    onChange=(value)=>{
        this.setState({type:value})
    }
    componentDidMount() {
        ajax('get','api/account?',(data)=>{
            this.setState({list:data.data})
        })
    }
    confirm=()=>{
        ajax('post',`api/account/delete?id=${this.state.id}&`,(data)=>{
            if (data.status){
                message.success('删除成功')
                setTimeout(()=>{
                    window.location.reload()
                },500)
            }else{
                message.error('删除失败');
            }
        })
    }
    cancel=()=>{
        message.error('Click on No');
    }
    render() {
        return(
            <div className={'my-vip'}>
                <div className={'top'}>
                    <Link className={'fl'} to={'/me'}>
                        <span className={'iconfont'}>&#xe609;</span>
                        <span>返回</span>
                    </Link>
                    <span className={'top-name'}>我的账户</span>
                    <Link to={'/me/myvip/add'} className={'fr'}>＋</Link>
                </div>
                <table className={'account-table'} style={this.state.tableStyle}>
                    <thead>
                        <tr>
                            <th>账户名</th>
                            <th>消费</th>
                            <th>操作</th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        this.state.list.map&&this.state.list.map((data)=>{
                            return(
                                <tr>
                                    <td>
                                        {data.name}
                                    </td>
                                    <td>
                                        {data.balance}
                                    </td>
                                    <td onClick={()=>{this.setState({id:data.id})}}>
                                        <Link to={`/me/myvip/edit/${data.id}`} className={'edit'}>
                                            编辑
                                        </Link>
                                        <Popconfirm
                                            title="确定要删除此帐户?"
                                            onConfirm={this.confirm}
                                            onCancel={this.cancel}
                                            okText="是"
                                            cancelText="否"
                                            className={''}
                                        >
                                            <a className={'delete'} href="#">删除</a>
                                        </Popconfirm>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
export default myvip