import React,{Component} from 'react'
import { createHashHistory } from 'history'
import ajax from "../../../ajax";
class notlog extends Component{
    componentDidMount() {
        ajax('get','api/user/logout?',(data)=>{
            if (data.data){
                createHashHistory().push('/log')
            }
        })
    }

    render() {
        return(
            <div></div>
        )
    }
}
export default notlog