import React,{Component} from 'react'
import ajax from '../../ajax'
import {Link} from "react-router-dom"
import { createHashHistory } from 'history'

class login extends Component{
    constructor(props) {
        super(props)
        this.state={
            nickname:'',
            password:'',
            mobile:"",
            captcha_code:'',
            imgPath:'',
            captcha_key:'',
            verify:''
        }
    }
    //设置高度
    height = window.screen.availHeight;
    section = {
        width: "100%",
        height: ((this.height)*1)+'px',
        background: `url(${require("../../img/login.jpg")})no-repeat center -0% /136%`
    }
//登录验证
    submit = ()=>{
            ajax('post','api/user/register?',(data)=>{
                if (data.status){
                    alert('注册成功,请登录')
                    createHashHistory().push('/log')
                }
            },this.state,(err)=>{
                alert(err)
            })
    }
//获取手机验证码
    getVerify = ()=> {
        ajax('post', 'api/sms/verify?', (data) => {
            if (data.data == 'INVALID_CAPTCHA') {
                alert('验证码错误')
                return
            }
            alert(data.data)
        }, this.state, (err) => {
            alert(err)
        })
    }
//挂载后获取图片
    componentDidMount(){
        ajax('GET','api/captcha?',(data)=>{
            this.setState({imgPath : data.data.url})
            this.setState({captcha_key : data.data.key})
            // console.log(data.data)
        })
    }
    render() {
        return(
            <div className='login-body' style={this.section}>
                <div className='login-border'>
                    <div className='login-nav'>
                        <Link to={'/log'}>
                            <span className='register-txt-log'>登录</span>
                        </Link>
                        <span className='register-txt-rog'>注册</span>
                        <p className='login-nav-bottom-r'></p>
                    </div>
                    <div className='login-input'>
                        <span><span> </span>昵称:</span><input onChange={(r)=>{this.setState({nickname:r.target.value})}} type="text"/>
                    </div>
                    <div className='login-input'>
                        <span>* 密码:</span><input onChange={(r)=>{this.setState({password:r.target.value})}} type="password"/>
                    </div>
                    <div className='login-input'>
                        <span>* 手机号:</span><input type="number" name='tel' onChange={(r)=>{this.setState({mobile:r.target.value})}}/>
                    </div>
                    <div className='login-input'>
                        <span>* 验证码:</span><input onChange={(r)=>{this.setState({captcha_code:r.target.value})}} maxLength='4' type="text"/>
                        <img className='image-verification' src= {this.state.imgPath} alt=""/>
                    </div>
                    <div className='login-input'>
                        <span>* 手机码:</span><input onChange={(r)=>{this.setState({verify:r.target.value})}} type="text"/>
                        <div className='tel-verification' onClick={this.getVerify}>获取</div>
                    </div>
                    <p className='login-remind'>!带*的选项为必填,其余为可选项</p>
                    <button onClick={this.submit}>注册</button>
                </div>
            </div>
        )
    }
}
export default login
