import React,{Component} from 'react'
import {Link} from "react-router-dom";
import '../../../../css/bookkeeping.css'
import '../../../../css/icon/iconfont.css'
import creatHistory from 'history/createHashHistory'
import ajax from "../../../../ajax";
import {createHashHistory} from "history";
const history = creatHistory()
class addbook extends Component{
    constructor(props) {
        super(props);
        this.state={
            name:''
        }
    }
    submit=()=>{
        ajax('post','api/book/create?',(data)=>{
            if (data.status){
                alert('添加成功')
                createHashHistory().push('/body/detail')
            }else{
                alert(data.data)
            }
        },this.state,(err)=>{
            alert(err)
        })
    }
    render() {
        return(
            <div className='addbook-all'>
                <div className='addbook-nav'>
                    <Link  onClick={history.goBack}>
                        <span className='iconfont'>&#xe609;</span>
                        <span>返回</span>
                    </Link>
                    <span className='addbook-book'>添加账本</span>
                </div>
                <div className='addbook-body'>
                    <div className='addbook-name cf'>
                        <span>名称</span>
                        <input className='book-name' type="text"
                               placeholder='请输入账户名称,最多四个中文'
                               onChange={(r)=>{this.setState({name:r.target.value})}}
                        />
                    </div>
                    <button onClick={this.submit}>添加</button>
                    <p>提示 :</p>
                    <p>1.账本的名称最多为四个字中文</p>
                    <p>2.别整一些花里胡哨的账本名</p>
                </div>
            </div>
        )
    }
}
export default addbook