import React,{Component} from 'react'
import {Link} from "react-router-dom";
import ajax from "../../../../ajax";
class body extends Component{
    constructor(props) {
        super(props);
        this.state={
            list:'',
        }
    }
    componentDidMount() {
        ajax('get','api/record/account?',(data)=>{
            console.log(data)
            if (data.status){
                this.setState({list:data.data.list})
            }
        })
    }

    render() {
        return (
            <ul className={'book-list-ul'}>
                <li>
                    <span className={'iconfont icon-txt'}></span>
                    <span className={'remark'}>收款人 : </span>
                    <span className={'paid'}>&nbsp;&nbsp;&nbsp;已付 : </span>
                </li>
                {
                    this.state.list.map&&this.state.list.map((data)=>{
                        return(
                            <li style={{display:data.status==10?'block':'none'}}>
                                <span className={'iconfont'}>{data.type_string=='支出'?'\ue605':'\ue61f'}</span>
                                <Link to={'/body/detail/edit/'+data.id}>
                                    <span className={'remark'}>{data.remark==''?'暂无':data.remark}</span>
                                    <span className={'paid'}>{data.type_string=='支出'?'-':'+'} {data.paid_money}</span>
                                </Link>
                            </li>
                        )
                    })
                }
            </ul>
        )
    }
}
export default body