import React,{Component} from 'react'
import { Select } from 'antd';
import {Link} from "react-router-dom";
import { createHashHistory } from 'history'
import ajax from "../../../../ajax";
const { Option } = Select;
class addAccCount extends Component{
    constructor(props) {
        super(props);
        this.state={
            id:'',
            name:'',
            type:1,
            initial_balance:0,
            remark:'',
            sort:10
        }
    }
    onChange=(value)=>{
        this.setState({type:value})
    }
    submit=()=>{
        ajax('post','api/account/create?',(data)=>{
            if (data.status){
                alert('添加成功')
                createHashHistory().push('/me/myvip')
            }else{
                alert(data.data)
            }
        },this.state,(err)=>{
            console.log(err)
        })
    }
    render() {
        return (
            <div className={'account-add-all'}>
                <div className={'top'}>
                    <Link className={'fl'} to={'/me/myvip'}>
                        <span className={'iconfont'}>&#xe609;</span>
                        <span>返回</span>
                    </Link>
                    <span className={'top-name'}>添加账户</span>
                    <span className={'add-btn'} onClick={this.submit}>
                          添加
                    </span>
                </div>
                <ul className={'add-account'}>
                    <li className={'account-name'}>
                        <span>账户名 :</span>
                        <input  type="text"
                               onChange={(r) => {
                                   this.setState({name: r.target.value})
                               }}
                               placeholder={'叫什么名字呢?'}/>
                    </li>
                    <li className={'add-type'}>
                        <span>类型名 :</span>
                       <Select
                           showSearch
                           defaultValue={"现金"}
                           optionFilterProp="children"
                           onChange={this.onChange}
                           filterOption={(input, option) =>
                               option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                           }
                       >
                           <Option value="1">现金</Option>
                           <Option value="2">银行</Option>
                           <Option value="3">支付平台</Option>
                           <Option value="4">其他</Option>
                       </Select>
                    </li>
                    <li className={'account-name'}>
                        <span>初余额 :</span>
                        <input  type="text"
                                onChange={(r) => {
                                    this.setState({initial_balance: r.target.value})
                                }}
                                placeholder={'选填'}/>
                    </li>
                    <li className={'account-name'}>
                        <span>写备注 :</span>
                        <input  type="text"
                                onChange={(r) => {
                                    this.setState({remark: r.target.value})
                                }}
                                placeholder={'选填'}/>
                    </li>
                </ul>
            </div>
        )
    }
}
export default addAccCount