import React,{Component} from 'react'
import {Link} from "react-router-dom";
import { createHashHistory } from 'history'
import ajax from "../../ajax";

class login extends Component{
    constructor(props) {
        super(props)
        this.state={
            password:'',
            mobile:"",
            captcha_code:'',
            imgPath:'',
            captcha_key:'',
        }
    }
    height = window.screen.availHeight;
    section = {
        width: "100%",
        height: ((this.height)*1)+'px',
        background: `url(${require("../../img/login.jpg")})no-repeat center -0% / 136%`
    }
    //登录
    submit = ()=>{
        ajax('post','api/user/token/mobile?',(data)=>{
            console.log(data)
            if (data.status){
                localStorage.setItem('token',data.data.token)
                createHashHistory().push('/body/detail')
            }else{
                alert(data.data)
            }
        },this.state,(err)=>{
            console.log(err)
        })
    }
//挂载后获取图片
    componentDidMount(){
        ajax('GET','api/captcha?',(data)=>{
            this.setState({imgPath : data.data.url})
            this.setState({captcha_key : data.data.key})
            // console.log(data.data)
        })
    }
    render() {
        return(
            <div className='login-body' style={this.section}>
                <div className='login-border'>
                    <div className='login-nav'>
                        <span className='login-txt'>登录</span>
                        <Link to={'/log/reg'}><span className='register-txt'>注册</span></Link>
                        <p className='login-nav-bottom-l'></p>
                    </div>
                    <div className='login-input'>
                        <span>手机号:</span><input type="number" onChange={(r)=>{this.setState({mobile:r.target.value})}} name='tel' maxLength='11'/>
                    </div>
                    <div className='login-input'>
                        <span>密码:</span><input  onChange={(r)=>{this.setState({password:r.target.value})}} type="password"/>
                    </div>
                    <div className='login-input'>
                        <span>验证码:</span><input   onChange={(r)=>{this.setState({captcha_code:r.target.value})}} maxLength='4' type="text"/>
                        <img className='image-verification' src= {this.state.imgPath} alt=""/>
                    </div>
                    <button onClick={this.submit}>登录</button>
                </div>
            </div>
        )
    }
}
export default login