import React,{Component} from 'react'
import {Link,Switch,Route} from "react-router-dom";
import '../../../css/bookkeeping.css'
import '../../../css/icon/iconfont.css'
import { createBrowserHistory } from 'history';
import axios from 'axios'
import ajax from "../../../ajax";
import qs from "qs";
import Out from './out'
import In from './in'
import {createHashHistory} from "history";
class category extends Component{
    constructor(props){
        super(props)
        this.state = {
            name:'',
            radio:'2',
            osty:{backgroundColor:'red'},
            isty:{backgroundColor:'rgba(0,0,0,0)'},
            display:'none',
            in:''
        }
    }
    submit=()=>{
        ajax('post','api/category/create?',(data)=>{
            if (data.status){
                alert('添加成功')
                this.setState({display:'none'})
                window.location.reload()
            }else{
                alert(data.data)
            }
        },{parent_id:0,type:this.state.radio,name:this.state.name,sort:1},(err)=>{
            alert(err)
        })
    }
    out=()=>{
        return('aaa')
    }
    componentDidMount() {
        console.log(createBrowserHistory)
    }

    render() {
        return(
            <div className={'category-all'}>
                <div className='category-top'>
                    <p className='top-nav'>
                            <Link className={'fl'}  to={'/body/detail'}>
                                <span className='iconfont'>&#xe609;</span>
                                返回
                            </Link>
                        <span className={'nav-name'}>个人账本-类别</span>
                    </p>
                    <div className={'top-select'}>
                        <span className={'tar'}>
                            <Link to={'/body/detail/category'}>支出</Link>
                        </span>
                        <span>
                            <Link to={'/body/detail/category/income'}>收入</Link>
                        </span>
                        <Switch>
                            <Route path={'/body/detail/category/income'}>
                                <div className={'category-select-bottom'}>
                                    <span style={{float:'right'}}></span>
                                </div>
                            </Route>
                            <Route path={'/body/detail/category'}>
                                <div className={'category-select-bottom'}>
                                    <span style={{float:'left'}}></span>
                                </div>
                            </Route>
                        </Switch>

                    </div>
                </div>
                <div className={'category-body'}>
                    <Switch>
                        <Route exact path={'/body/detail/category'}>
                                <Out/>
                        </Route>
                        <Route exact path={'/body/detail/category/income'}>
                            <In></In>
                        </Route>
                    </Switch>
                </div>
                <div className={'category-bottom'}>
                   <button>保存排序</button>
                   <button onClick={()=>{this.setState({display:'block'})}}>添加类别</button>
                </div>
                <div className={'category-add'} style={{display:this.state.display}}>
                    <div className={'form'}>
                        <div className={'submit'}>
                            <button onClick={()=>{
                                this.setState({display:'none'})
                            }}>取消</button>
                            <button onClick={this.submit}>添加</button>
                        </div>
                        <div className={'radio-select'}>
                            <div className={'radio'}>
                                <div
                                    style={this.state.osty}
                                    onClick={()=>{this.setState(
                                        {
                                            radio:'1',
                                            osty:{backgroundColor:'red'},
                                            isty:{backgroundColor:'rgba(0,0,0,0)'}
                                        }
                                    )}}
                                    >支出
                                </div>
                                    <div
                                        style={this.state.isty}
                                        onClick={()=>{this.setState(
                                        {
                                            radio:'1',
                                            isty:{backgroundColor:'red'},
                                            osty:{backgroundColor:'rgba(0,0,0,0)'}
                                        }
                                    )}}
                                        >收入
                                    </div>
                            </div>
                        </div>
                        <div className={'category-add-txt'}>请输入类别名,</div>
                        <div className={'category-add-txt'}>最多四字中文.</div>
                        <input type="text"  onChange={(r)=>{this.setState({name:r.target.value})}}/>
                    </div>
                </div>
            </div>
        )
    }
}
export default category