import React,{Component} from 'react'
import {Link} from "react-router-dom";
import '../../../css/bookkeeping.css'
import ajax from "../../../ajax";
import { Upload, Icon, message } from 'antd';
function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
}

class metop extends Component{
    constructor(props) {
        super(props)
        this.state={
            toggle:true,
            nickname :'',
            avatarName:{display:'none'},
            avatar:'',
            nameStyle:{display:'none'},
            token:'',
            load:{loading: false},
            upUrl:''
        }
        this.fileInput=React.createRef()

    }
    handleChange = info => {
        if (info.file.status === 'uploading') {
            this.setState({ loading: true });
            return;
        }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            getBase64(info.file.originFileObj, imageUrl =>
                this.setState({
                    imageUrl,
                    loading: false,
                }),
            );
        }
    };
    setAvatar=()=>{
        if(this.state.toggle){
            this.setState({avatarName:{display: 'block'},toggle:false})
        }else {
            this.setState({avatarName:{display: 'none'},toggle:true})
        }
    }
    cancel=()=>{
        if(this.state.toggle){
            this.setState({nameStyle:{display: 'block'},toggle:false})
        }else {
            this.setState({nameStyle:{display: 'none'},toggle:true})
        }
    }
    setName=()=>{
        ajax('post','api/user/profile/update?',(data)=>{
            console.log(data)
            window.location.reload()
        },{nickname:this.state.nickname})
    }
    componentDidMount(){
        if (localStorage.getItem('token')!=null){
            ajax('GET','api/user/profile?',(data)=>{
                this.setState({nick:data.data.nickname,
                    avatar:data.data.avatar_url,
                    token:localStorage.getItem('token'),
                    upUrl:'http://jizhang-api-dev.it266.com/api/upload/image?token='+localStorage.getItem('token')})
            },'',(err)=>{alert(err)})
        }
    }
    render() {
        const uploadButton = (
            <div>
                <Icon type={this.state.loading ? 'loading' : 'plus'} />
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        const { imageUrl } = this.state.load;
        if (localStorage.getItem('token')!=null){
            return(
                <div className='me-top-notlog' >
                    <div className='me-top-img'
                         onClick={()=>{
                             this.setState({nameStyle:{display:'none'}})
                         }}>
                        <img src={this.state.avatar} alt="" onClick={this.setAvatar}/>
                    </div>
                    <p className='nickname' >
                        {console.log(this.state.upUrl)}
                        <div className={'set-avatar'} style={this.state.avatarName}>
                           <Link to={'/me/upimg'}>更换头像</Link>
                        </div>
                            <span className={'name'}>{this.state.nick}</span>
                            <span className={'iconfont'} onClick={this.cancel}>
                                &#xe60f;
                            </span>
                        <div className={'set-nickname'} style={this.state.nameStyle}>
                            <input type="text" onChange={(v)=>{this.setState({nickname :v.target.value})}}/>
                            <span onClick={this.setName}>更改</span>
                        </div>
                    </p>
                </div>
            )
        } else {
            return(
                <div className='me-top-notlog'>
                    <Link to='/log'>登录账号,开始记账</Link>
                </div>
            )
        }
    }
}
export default metop