import React,{Component} from 'react'
import ajax from "../../../../ajax";
import axios from "axios";
import qs from "qs";
class out extends Component{
    constructor(props) {
        super(props);
        this.state={
            out:'',
        }
        let theUrl=''
        if (localStorage.getItem('token')!=null){
            theUrl='http://jizhang-api-dev.it266.com/api/category?token='
                +localStorage.getItem('token')+'&type='
        }else{
        }
        axios({
            method:'get',
            url:theUrl+'2',
            headers:{
                "Content-Type": "application/x-www-form-urlencoded"
            },
            data:qs.stringify({type: 1})}).then((response)=>{
            this.setState({out:response.data})
        })
    }
    render() {
        return(
            <ul className='category-body-ul'>
                {
                    this.state.out.data&&this.state.out.data.map((data)=>{
                        return(<li id={data.id}>
                            <span>{data.name}</span>
                            <span className='iconfont'
                                  onClick={()=> {
                                          axios({
                                              method: 'post',
                                              url: 'http://jizhang-api-dev.it266.com/api/category/delete?id=' + data.id + '&token=' + localStorage.getItem('token'),
                                              headers: {
                                                  "Content-Type": "application/x-www-form-urlencoded"
                                              },
                                              data: ''
                                          }).then((response) => {
                                              console.log(response)
                                              if (response.data.status) {
                                                  alert('删除成功')
                                                  window.location.reload()
                                              } else {
                                                  alert(response.data)
                                              }
                                          })}}
                                      >X</span>
                        </li>)
                    })
                }
            </ul>
        )
    }
}
export default out